import React from 'react';
import { BrowserRouter as Router, Route } from 'react-router-dom'; // Import BrowserRouter instead of Switch
import { Container } from '@mui/material';
import ListaProveedores from './components/ListaProveedores';
import CrearProveedor from './components/CrearProveedor';
import EditarProveedor from './components/EditarProveedor';
import VentanaEmergente from './components/VentanaEmergente';

const App = () => {
  return (
    <Router> {/* Use BrowserRouter directly */}
      <Container maxWidth="lg">
        {/* Render routes using Route component */}
        <Route exact path="/" component={ListaProveedores} />
        <Route exact path="/create" component={CrearProveedor} />
        <Route exact path="/edit/:id" component={EditarProveedor} />
        <Route exact path="/screening/:id" component={VentanaEmergente} />
      </Container>
    </Router>
  );
};

export default App;

